import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import classNames from 'classnames';
import styled from 'styled-components';
import { BREAKPOINT_LG,BLUE_COLOR,TRANSITION,DARK_FONT_COLOR, BREAKPOINT_MDL,ICON_BASE_LG,FONT_COLOR,FLEX,ICON_BASE,ICON_BASE_MD,FLEX_LG,FLEX_SM,FLEX_MD,ICON_SIZE,ICON_SIZE_MD,SECONDARY_FONT_COLOR,ICON_NAME_COLOR,DARK_BORDER_COLOR,FADE_FONT_COLOR, BREAKPOINT_MD, BREAKPOINT_SM, FONT_FAMILY_HEADER, LIGHT_BLUE_COLOR, ICON_RADIUS} from '../types';
import { changeSideIcon, setSelectedIcon } from '../../actions/iconActions';
const IconBox = styled.div`
  animation-fill-mode: forwards;
margin:7px 10px; 
box-shadow: 0 0px 0px 1px rgba(0,0,0,0.05);

 &.active{
  background:#eee;
 }
   .new-dot{
      background: ${BLUE_COLOR};
    width: 5px;
    height: 5px;
    border-radius: 50%;
    position: absolute;
    right: 5px;
    top: 5px;
    }
            i {
              color: ${props => props.dark ? DARK_FONT_COLOR : FONT_COLOR};
           
                font-size: ${ICON_SIZE};  
                 @media screen and (max-width: ${BREAKPOINT_LG}) {
                font-size: ${ICON_SIZE}; 
              }
              
              
                @media screen and (max-width: ${BREAKPOINT_MD}) {
                font-size: ${ICON_SIZE_MD}; 
              }
              }
              &.fade{
                &.active{
                  .icon-base {
                opacity:1;
              }
                }
                .icon-base {
                opacity:0.5;
              }
              }
              
            cursor: pointer;
            outline: none !important;
           display:inline-block;

            
                    
            
            
             &:hover{
                 z-index: 9999999;
                 .icon-box-inner{
        background:#fff;
        
      }
                 box-shadow: 0 0 10px 0px rgba(0,0,0,0.1);
    }  
    &.active{
      &:hover{
        box-shadow: none;
      }

    }
    &:active{
  
      .icon-box-inner{
        background:rgba(0,0,0,.01);
        
      }
    }
    transition:${TRANSITION};
    &:focus{
 
  outline: 0;
}
    border-radius: ${ICON_RADIUS};overflow: hidden;
               overflow: hidden;
              
            
   
            .icon-box-inner {
              margin:0px;
              font-weight: 700;
              position: relative;
              font-size: 16px;
              border-radius: 5px;
              line-height: 35px;
              text-align: center;
              pointer-events: none;
              
              .icon-base{
                height:${ICON_BASE};
               
                i{
                  line-height:${ICON_BASE};
                }
                @media screen and (max-width: ${BREAKPOINT_LG}) {
                  height:${ICON_BASE_LG};
                i{
                  line-height:${ICON_BASE_LG};
                }
              }
                @media screen and (max-width: ${BREAKPOINT_MD}) {
                  height:${ICON_BASE_MD};
                i{
                  line-height:${ICON_BASE_MD};
                }
              }

              }
              .icon-box-name {
                  font-size: 13px;
                  -webkit-touch-callout: none;
    -webkit-user-select: none; 
    text-transform:capitalize;
     -khtml-user-select: none; 
       -moz-user-select: none; 
        -ms-user-select: none; 
            user-select: none;
            width:80px;
            font-family:${FONT_FAMILY_HEADER};
                  font-weight: 400;
                  color: ${ICON_NAME_COLOR};
                  @media screen and (max-width: ${BREAKPOINT_LG}) {
                display:none;
              }
                  margin: 0px 5px;
                  text-overflow: ellipsis;
                  overflow: hidden;
                  white-space: nowrap;
                  line-height: 20px;    margin-top: 10px; margin-bottom: 2px;
              }
              padding: 25px 11px;
              padding-bottom:15px;
        
             @media screen and (max-width: ${BREAKPOINT_LG}) {
                width: auto;
                padding: 20px ;
              }
             

            }
            &:active {
    border-radius: 5px;
}


            &.active{
   border-radius: 5px;
   background:#eee;
}
@media screen and (max-width: ${BREAKPOINT_MDL}) {
  cursor: initial;    margin: 3px 7px;
  &.active,&:hover,&:active { box-shadow: none;background:transparent;}     
              }
        `;
class IconsListItem extends Component {
    constructor(props) {
        super(props);
        this.handleBoxClick = this.handleBoxClick.bind(this);
        this.state = {
            selectedIcon: {
                name: null,
                slug: null,
                type_of_icon:null,
            },
            searchTerm: '',

        };
    }

    handleBoxClick(name, slug, type_of_icon) {
        this.props.changeSideIcon(true);
        this.props.setSelectedIcon(name, slug, type_of_icon);

    }
    componentWillReceiveProps(newProps) {
      
        this.setState({
            selectedIcon: newProps.selectedIcon
        });
        this.setState({
            searchTerm: newProps.searchTerm
        });
       
        

    }
    render() {
        
        var boxClass = classNames({
            'active': (this.state.selectedIcon.slug === this.props.icon.slug),
            'icon-box ':true,
        });
        return (
            <IconBox dark={this.props.dark} className={boxClass} ref="iconbox" tabIndex={this.props.num} onFocus={() => this.handleBoxClick(this.props.icon.name, this.props.icon.slug, this.props.icon.type_of_icon)}>
  
      <div className="icon-box-inner" >
      {this.props.icon.new&&<div className="new-dot"></div>}
      <div className="icon-base" >
      {this.props.icon.type_of_icon==='REGULAR' && <i className={"fadeIn animated bx bx-"+this.props.icon.name}></i>}
      {this.props.icon.type_of_icon==='SOLID' && <i className={"fadeIn animated bx bxs-"+this.props.icon.name}></i>}
 {this.props.icon.type_of_icon==='LOGO' && <i className={"fadeIn animated bx bxl-"+this.props.icon.name}></i>}
</div>
<div className="icon-box-name">{this.props.icon.name}</div>
     </div></IconBox>
        );
    }
}
;

function mapStateToProps(state) {
    return {
        selectedIcon: state.selectedIcon,
        searchTerm: state.searchTerm,
    };
}
function mapDispatchToProps(dispatch) {
    return bindActionCreators({
        changeSideIcon,
        setSelectedIcon
    }, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(IconsListItem);
